@font-face {
  font-family: 'Cunia';
  src: url('../fonts/Cunia.otf') format('opentype');
}

@font-face {
  font-family: 'Wanderlust';
  src: url('../fonts/Wanderlust.ttf') format('truetype');
}

/* For icons */
@font-face {
  font-family: 'Pixeden7';
  src: url('../fonts/icons/Pe-icon-7-stroke.eot?d7yf1v');
  src: url('../fonts/icons/Pe-icon-7-stroke.eot?#iefixd7yf1v')
      format('embedded-opentype'),
    url('../fonts/icons/Pe-icon-7-stroke.woff?d7yf1v') format('woff'),
    url('../fonts/icons/Pe-icon-7-stroke.ttf?d7yf1v') format('truetype'),
    url('../fonts/icons/Pe-icon-7-stroke.svg?d7yf1v#Pe-icon-7-stroke')
      format('svg');
  font-weight: normal;
  font-style: normal;
}
:root {
  --screen-md-min: 768px;
  --screen-md-max: 992px;
}
